@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    50% {
        opacity: .4;
    }
    100% {
        opacity: 0;    
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    50% {
        opacity: .4;
    }
    100% {
        opacity: 0;    
    }
}

@-webkit-keyframes scaleIn {
    0% { /* IE 9 */
        -webkit-transform: scale(0.75, 0.75); /* Safari */
        transform: scale(0.75, 0.75);
        opacity: 0;
    }

    100% { /* IE 9 */
        -webkit-transform: scale(1, 1); /* Safari */
        transform: scale(1, 1);
        opacity: 1;    
    }
  }

@keyframes scaleIn {
    0% { /* IE 9 */
        -webkit-transform: scale(0.75, 0.75); /* Safari */
        transform: scale(0.75, 0.75);
        opacity: 0;
    }

    100% { /* IE 9 */
        -webkit-transform: scale(1, 1); /* Safari */
        transform: scale(1, 1);
        opacity: 1;    
    }
  }

  @-webkit-keyframes zoomIn {
    0% { /* IE 9 */
        -webkit-transform: scale(1, 1); /* Safari */
        transform: scale(1, 1);
    }

    100% { /* IE 9 */
        -webkit-transform: scale(1.05, 1.05); /* Safari */
        transform: scale(1.05, 1.05);
    }
  }

  @keyframes zoomIn {
    0% { /* IE 9 */
        -webkit-transform: scale(1, 1); /* Safari */
        transform: scale(1, 1);
    }

    100% { /* IE 9 */
        -webkit-transform: scale(1.05, 1.05); /* Safari */
        transform: scale(1.05, 1.05);
    }
  }

